import React from "react";
import Item from "./Item";
import {Divider , Input, Box} from "@chakra-ui/react"

class ToDoDisplay extends React.Component{

    render(){
        return(
            <>
                <Box p="30px" pb="60px"  borderWidth="1px" borderRadius="lg" overflow="hidden">
                    {                   
                        this.props.todoList.map((item, index) => {
                            return(
                                <>
                                <Item index={index} onDelete={this.props.onDelete} key={index} item={item}/> 
                                <Divider/>                               
                                </>
                            )
                        })
                    }
                </Box>                
            </>
        );
    };
}

export default ToDoDisplay;